.footer {
    background: #ebedf2;
    color: #333;
}

.footer a {
    color: #333;
}
.footer a i{
    margin-left: 10px;
    font-size: 30px;
}

.footer-container {
    max-width: 1100px;
    margin: auto;
    padding: 40px;
}

.footer-lists {
    display: flex;
    justify-content: space-between;
}

.footer-lists .list-head {
    text-transform: uppercase;
    font-weight: bold;
    margin-bottom: 5px;
}

.divider {
    width: 100%;
    height: 3px;
    border-top: 1px solid #ccc;
    margin: 30px 0;
}

.social-icons img {
    width: 25px;
    height: 25px;
    margin-right: 25px;
}

@media (max-width: 768px) {


    .footer-lists {
        flex-direction: column;
    }

    .footer-lists .list-head {
        margin-top: 10px;
    }

    
}